import React, { useEffect, useState } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Switch } from "react-router-dom";
import { pendingdoctorlist, approvedoctor } from "../reduxx/actions/actions";

function PendingList() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(pendingdoctorlist());
  }, []);

  var pendinglist = useSelector(
    ({ pendingdoctorlist }) => pendingdoctorlist.payload
  );
  const [show, setShow] = useState(false);
  const [id, setId] = useState("");
  const [reason, setReason] = useState();
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const handleClick = () => {
    dispatch(approvedoctor({ id: id, status: "rejected", review: reason }));
    handleClose();
  };
  return (
    <div className="row">
      <div className="col-12 grid-margin">
        <div className="card">
          <div className="card-body">
            <h4 className="card-title"> Pending List </h4>
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th> User </th>
                    <th> Doctor Regno </th>
                    <th> Designation </th>
                    <th>Phone number</th>
                    <th>Phone number status</th>
                    <th> Joined Date</th>
                    <th> Location </th>
                    <th> Status </th>
                    <th> Actions </th>
                  </tr>
                </thead>
                <tbody>
                  {Array.isArray(pendinglist) && pendinglist.length > 0 ? (
                    pendinglist.map((item) => {
                      return (
                        <tr>
                          <td>
                            <img src={item.photo} className="mr-2" alt="face" />
                            {item.name}
                          </td>
                          <td> {item.doc_reg_no} </td>
                          <td> {item.designation} </td>
                          <td> {item.mobile} </td>
                          <td
                            style={{
                              display: "flex",
                              justifyContent: "center",
                            }}
                          >
                            {" "}
                            {item.ph_verified == false ? "No" : "Yes"}{" "}
                          </td>

                          <td> {item.createdAt}</td>
                          <td> {item.district} </td>
                          <td>
                            <label className="badge badge-gradient-danger">
                              {item.approval_status
                                .substring(0, 1)
                                .toUpperCase() +
                                item.approval_status.substring(
                                  1,
                                  item.approval_status.length
                                )}
                            </label>
                          </td>
                          <td>
                            <select
                              class="custom-select"
                              onChange={(event) => {
                                switch (event.target.value) {
                                  case "approved":
                                    dispatch(
                                      approvedoctor({
                                        id: item.id + "",
                                        status: "approved",
                                      })
                                    );
                                    break;
                                  case "rejected":
                                    setId(item.id + "");
                                    handleShow();
                                  default:
                                    break;
                                }
                                event.target.value = "update_status";
                              }}
                            >
                              <option value="update_status">
                                Update Status
                              </option>
                              <option value="approved">Approve</option>
                              <option value="rejected">Reject</option>
                            </select>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <tr>
                      <td colspan="7" style={{ textAlign: "center" }}>
                        No Data Found
                      </td>
                    </tr>
                  )}
                </tbody>

                <Modal show={show} onHide={handleClose}>
                  <Modal.Header closeButton style={{ backgroundColor: "#fff" }}>
                    <Modal.Title>Reason</Modal.Title>
                  </Modal.Header>
                  <Modal.Body
                    style={{ backgroundColor: "#fff", color: "black" }}
                  >
                    <Form.Control
                      type="text"
                      name="reason"
                      placeholder="Enter reason"
                      onChange={(e) => setReason(e.target.value)}
                      aria-describedby="passwordHelpBlock"
                    />
                  </Modal.Body>
                  <Modal.Footer
                    style={{ backgroundColor: "#fff", color: "black" }}
                  >
                    <Button variant="danger" onClick={handleClick}>
                      Reject
                    </Button>
                  </Modal.Footer>
                </Modal>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PendingList;
